import { string } from 'yup'
import AQUAMAN from '../images/Teamlogo/Aquaman.jpg'
import Arryn from '../images/Teamlogo/Arryn.jpg'
import Baratheon from '../images/Teamlogo/Baratheon.jpg'
import Batman from '../images/Teamlogo/Batman.jpg'
import BlackPanter from '../images/Teamlogo/BlackPanter.png'
import CaptainAmerica from '../images/Teamlogo/Captain-America.jpg'
import DrStrange from '../images/Teamlogo/DrStrange.jpg'
import Falcon from '../images/Teamlogo/Falcon.jpg'
import Flash from '../images/Teamlogo/Flash.jpg'
import Greyjoy from '../images/Teamlogo/Greyjoy.jpg'
import Groot from '../images/Teamlogo/Groot.jpg'
import Hawkeye from '../images/Teamlogo/Hawkeye.jpg'
import Hulk from '../images/Teamlogo/Hulk.png'
import IronMan from '../images/Teamlogo/IronMan.jpg'
import Joker from '../images/Teamlogo/Joker.jpg'
import Lannister from '../images/Teamlogo/Lannister.jpg'
import Loki from '../images/Teamlogo/Loki.jpg'
import Magneto from '../images/Teamlogo/Magneto.jpeg'
import Mormont from '../images/Teamlogo/Mormont.jpg'
