import Thor from '../images/Teamlogo/Thor.png'
import Tully from '../images/Teamlogo/Tully.jpg'
import Tyrell from '../images/Teamlogo/Tyrell.jpg'
import Wolverine from '../images/Teamlogo/Wolverine.jpg'

function TEAMLOGOINFO() {

    const GetImageName = (x)=>{
        let imagePath = String(x);
        return String(x).substring(imagePath.lastIndexOf('/')+1,imagePath.indexOf('.'))
    }
    let ImageInfo = [AQUAMAN,Arryn,Baratheon,Batman,BlackPanter,CaptainAmerica,DrStrange,Falcon,Flash,Greyjoy,Groot,Hawkeye,Hulk,IronMan,Joker,Lannister,Loki,Magneto,Mormont,Spiderman,Stark,Superman,Targaryen,Thanos,Thor,Tully,Tyrell,Wolverine];
    return ImageInfo.map((x)=>{
        return {"ImageName":GetImageName(x),"ImagePath":x}
    })
}

export default TEAMLOGOINFO;