import './index.css';
import { useEffect, useState, CSSProperties } from 'react'
import Alert from 'react-bootstrap/Alert';
import { Controller, useForm } from "react-hook-form";
import Select from 'react-select'
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { useParams, useSearchParams } from 'react-router-dom';
function REGISTER() {

    const { register, handleSubmit, watch, control, formState: { errors }, setValue, getValues } = useForm();
    const [corpId, setCorpId] = useState("");
    const [name, setName] = useState("");
    const [teamMemberList, setTeamMemberList] = useState([]);
    const [showAlert, setShowAlert] = useState({ alertError: "", isVisible: false });
    const params = useParams();
    useEffect(() => {
        var toastElList = [].slice.call(document.querySelectorAll('.toast'))
    }, [])

    const corpIdPattern = () => {
        return '(A|a)[0-9]{6}'
    }

    const showErrorAlertBox = (errorMessage) => {
        setShowAlert({ alertError: errorMessage, isVisible: true });
        triggerHideError()
    }

    const triggerHideError = () => {
        setTimeout(() => setShowAlert({ isVisible: false }), 5000)
    }
    const isValidCorpId = (text) => {
        let match = text.match(corpIdPattern());
        return match;
    }
    const myData = [
        { value: 'chocolate', label: 'Football' },
        { value: 'strawberry', label: 'Cricket' },
        { value: 'vanilla', label: 'Volleyball' }
    ]
    const cityName = [
        { value: 'Chennai', label: 'Chennai' },
        { value: 'Banglore', label: 'Banglore' }
    ]

    useEffect(() => {
        if (params.teamName) {
            setValue('TeamName', params.teamName)
        }

        if (params.corpId) {
            let apiData = JSON.parse("{\"TeamName\":\"TeamTwo\",\"CorpId\":\"a728575\",\"CaptainName\":\"Vijay\",\"ContactNumber\":\"9025250494\",\"Password\":\"Vijay\",\"select\":{\"value\":\"Chennai\",\"label\":\"Chennai\"},\"teamList\":[{\"corpId\":\"a728685\",\"name\":\"Vijay\",\"isEdit\":false}]}");

            Object.keys(apiData).forEach((x) => {
                console.log(typeof (x))
                if (typeof (x) === "string") {
                    setValue(x, apiData[x])
                }
            })
            setTeamMemberList(apiData.teamList)
        }
    })

    const isCorpIdAlreadyExists = (corpId) => {
        return teamMemberList.filter((value, index) => index != teamMemberList.length - 1).find(x => x.corpId.toLowerCase() == corpId.toLowerCase());
    }
    const groupBadgeStyles: CSSProperties = {
        backgroundColor: '#EBECF0',
        borderRadius: '2em',
        color: '#172B4D',
        display: 'inline-block',
        fontSize: 12,
        fontWeight: 'normal',
        lineHeight: '1',
        minWidth: 1,
        padding: '0.16666666666667em 0.5em',
        textAlign: 'center',
    };


    const onSubmit = (data) => {
        teamMemberList.forEach((x) => {
            if (x.isEdit) {
                showErrorAlertBox("Team Member table is incomplete")
                return
            }
            if (x.corpId === getValues("CorpId")) {
                showErrorAlertBox("Team Captain Corp ID is already their remove from the team member ")
                return
            }

        })
        data.teamList = teamMemberList
        console.log(JSON.stringify(data))
    }
    return (<div className=" w-100 h-100" >

        <form className="  w-100  row p-3 h-100 row justify-content-center" style={{ borderRadius: "20px" }} onSubmit={handleSubmit(onSubmit)}>

            <h1 class="col-md-12 text-center text-color-s" style={{ marginBottom: "20px" }}>
                REGISTER YOUR TEAM!!!!!
            </h1>

            <div className={`col-md-12 ${showAlert.isVisible ? "" : "hide"}`}>
                <Alert variant="danger" show={showAlert.isVisible}>
                    <Alert.Heading>Error
                    </Alert.Heading>
                    <hr />
                    <p className="mb-0">
                        {showAlert.alertError}
                    </p>
                </Alert>
            </div>
            <div className='row justify-content-center'>
            <div class="form-floating col-md-2 col-sm-12">
                <input type="text" class="form-control" disabled={true} id="teamName" value={params.teamName} placeholder="Team Name" autoComplete={"off"} {...register("TeamName", { required: true })} />
                <label for="teamName" className='customLabel'>Team Name</label>
                {errors?.TeamName && <span className='error'>This field is required</span>}
            </div>
            <div class="form-floating col-md-2 col-sm-12">
                <input type="text" class="form-control" id="teamCaptainCorpId" placeholder="Team Caption Corp Id" autoComplete={"off"} {...register("CorpId", { required: true })} />
                <label for="teamCaptainCorpId" className='customLabel'>Team Captain Corp Id</label>
                {errors?.CorpId && <span className='error'>This field is required</span>}
            </div>
            <div class="form-floating col-md-2 col-sm-12">
                <input type="text" class="form-control" id="teamCaptainName" placeholder="Team Captain Name" autoComplete={"off"} {...register("CaptainName", {
                    required: true, pattern: {
                        value: corpIdPattern(),
                        message: "Invalid CorpId"
                    }
                })} />
                <label for="teamCaptainName" className='customLabel'>Team Captain Name</label>
                {errors?.CaptainName && <span className='error'>This field is required</span>}
            </div>
            <div class="form-floating col-md-2 col-sm-12">
                <input type="text" class="form-control" id="contactNumber" placeholder="Contact Number" autoComplete={"off"} {...register("ContactNumber", { required: true })} />
                <label for="contactNumber" className='customLabel'>Contact Number</label>
                {errors?.ContactNumber && <span className='error'>This field is required</span>}
            </div>
            </div>
            <div className='row justify-content-center'>
            <div class="dropdown col-md-2 col-sm-12 customSelect ">
                <Controller
                    rules={{ required: true }}
                    name="select"
                    control={control}
                    render={({ field, fieldState }) => <Select
                        value={cityName.find((c) => c.value === field.value)}
                        onChange={(val) => field.onChange(val.map((c) => c.value))}
                        options={cityName}
                    />}
                />
                {errors?.select && <span className='error'>This field is required</span>}
            </div>
            
            <div class="form-floating col-md-2 col-sm-12" style={{ display: params.corpId ? "hidden" : "" }}>
                <input type="password" class="form-control" id="contactNumber" placeholder="Password" autoComplete={"off"} {...register("Password", { required: true })} />
                <label for="contactNumber" className='customLabel'>Password</label>
                <span style={{ marginTop: "30px", color: "var(--secoundary)" }}>Note:Use Password to edit the user info in edit screen</span>
                {errors?.ContactNumber && <><br></br><span className='error'>This field is required</span></>}
            </div>
            </div>
            
            {/* <div className='col-md-2 col-sm-12 customSelect' id='customSelect'>
                <Controller
                    rules={{ required: true }}
                    name="multiselect"
                    control={control}
                    render={({ field, fieldState }) => <Select
                        options={myData}
                        selectMultiple={true}
                        touchUi={false}
                        isMulti
                    />}
                />
                {errors?.multiselect && <span className='error'>This field is required</span>}

            </div> */}
            <div className="col-md-12 row teamMemberSection" id="" >
                <h4 class="text-color-s text-center col-md-12" style={{ marginTop: "8px" }}>
                    Add Your Team Member
                </h4>
                <div class="table-responsive col-md-12 " style={{ fontSize: '12px', maxHeight: "250px" }}>
                    <table class="table caption-top ">

                        <thead>
                            <tr>
                                <th scope='col'>Sl.No</th>
                                <th scope="col text-center">CorpId</th>
                                <th scope="col text-center">Name</th>
                                <th scope='col'></th>
                            </tr>
                        </thead>
                        <tbody>
                            {teamMemberList && teamMemberList.map((x, index) => {
                                return (
                                    <tr>
                                        <th scope="row">
                                            {index + 1}
                                        </th>
                                        <th scope="row">
                                            {x.isEdit ? <input type={"text"} id={`customInputCorpId${index}`} className='customInput' role="presentation" autoComplete={"off"} value={x.corpId} onChange={(e) => {
                                                x.corpId = e.target.value
                                                setTeamMemberList([...teamMemberList])
                                            }}></input> : x.corpId}

                                        </th>
                                        <td>
                                            {x.isEdit ? <input type={"text"} id={`customInputName${index}`} className='customInput' value={x.name} role="presentation" autoComplete={"off"} onChange={(e) => {
                                                x.name = e.target.value
                                                setTeamMemberList([...teamMemberList])
                                            }}></input> : x.name}
                                        </td>
                                        <td>
                                            <div className='btn btn-warning' onClick={() => {
                                                debugger
                                                if (!x.isEdit) {
                                                    teamMemberList[index].isEdit = true;
                                                    setTeamMemberList([...teamMemberList])
                                                    return;
                                                }
                                                let corpId = document.getElementById(`customInputCorpId${index}`).value;
                                                let name = document.getElementById(`customInputName${index}`).value;
                                                if (corpId === "" || name === "") {
                                                    showErrorAlertBox("Please Enter the valid data in table")
                                                    return
                                                }
                                                if (!isValidCorpId(corpId)) {
                                                    showErrorAlertBox("Given CorpId is not valid")
                                                    return
                                                }

                                                teamMemberList[index].corpId = corpId;
                                                teamMemberList[index].name = name;
                                                teamMemberList[index].isEdit = false;
                                                setTeamMemberList([...teamMemberList])
                                            }}>{x.isEdit ? "Save" : "Edit"}</div>
                                            <div className="btn btn-danger " style={{ marginLeft: "20px" }} onClick={() => {
                                                setTeamMemberList([...teamMemberList.filter((value, valueIndex) => valueIndex != index)])
                                            }}>Delete</div>
                                        </td>
                                    </tr>
                                )
                            })}
                            {/* <tr>

                                <th scope="row">

                                </th>
                                <td>
                                    <input type="text" id="CorpId" onChange={(e) => {
                                        setCorpId(e.target.value)
                                    }} className='customInput' value={corpId} />
                                </td>
                                <td>
                                    <input type="text" id="Name" className='customInput' onChange={(e) => {
                                        setName(e.target.value)
                                    }} value={name} />
                                </td>
                                <td></td>
                            </tr> */}
                        </tbody>
                    </table>

                </div>
                <div className='btn btn-success w-100 col-md-12 d-flex align-items-center justify-content-center' onClick={() => {

                    if (teamMemberList.length > 0) {

                        if (teamMemberList.length > 10) {
                            showErrorAlertBox("Team Member must be 10 or less than 10")
                            return;
                        }
                        let corpId = document.getElementById(`customInputCorpId${teamMemberList.length - 1}`)?.value;
                        let name = document.getElementById(`customInputName${teamMemberList.length - 1}`)?.value;
                        if (corpId != undefined || name != undefined) {
                            if (!teamMemberList[teamMemberList.length - 1].isEdit) {
                                return;
                            }
                            if (corpId === "" || name === "") {
                                showErrorAlertBox("Please Enter the valid data in table")
                                return
                            }
                            if (!isValidCorpId(corpId)) {
                                showErrorAlertBox("Given CorpId is not valid")
                                return
                            }
                            if (isCorpIdAlreadyExists(corpId)) {
                                showErrorAlertBox("CorpId is already exists")
                                return
                            }
                            teamMemberList.forEach((x) => {
                                x.isEdit = false;
                            })
                            teamMemberList[teamMemberList.length - 1].corpId = document.getElementById(`customInputCorpId${teamMemberList.length - 1}`).value;
                            teamMemberList[teamMemberList.length - 1].name = document.getElementById(`customInputName${teamMemberList.length - 1}`).value;
                            teamMemberList[teamMemberList.length - 1].isEdit = false;
                        }


                    }

                    setTeamMemberList([...teamMemberList, { corpId: "", name: "", isEdit: true }])
                }}>+ ADD</div>
                <div className='col-md-12  d-flex align-items-center justify-content-center' style={{ marginTop: "20px" }}>
                    <button class='btn btn-success' >Submit</button>
                </div>
            </div>
        </form>
        <div role="alert" aria-live="assertive" aria-atomic="true" class="toast" data-bs-autohide="false">
            <div class="toast-header">
                <img src="..." class="rounded me-2" alt="..." />
                <strong class="me-auto">Bootstrap</strong>
                <small>11 mins ago</small>
                <button type="button" class="btn-close" data-bs-dismiss="toast" aria-label="Close"></button>
            </div>
            <div class="toast-body">
                Hello, world! This is a toast message.
            </div>
        </div>
    </div>);
}

export default REGISTER;