import LOGO from '../../images/2.jpg'
import '../HomePage/index.css'
import {useState} from 'react'
import { useNavigate } from 'react-router-dom'
import TEAMLOGOINFO from '../../utils/teamlogo';
function CHOOSETEAMANME() {
    const navigate = useNavigate();
    const [selectedIndex,setSelectedIndex] = useState(-1)
    const teamNames = TEAMLOGOINFO();
    
    return (
        <div className="container-fluid" style={{ marginTop: "2%", height:"100%" }}>
            <div className='row'>
                <h4 className='customHeading text-center'>Available Team Name</h4>
            </div>
            <div className="row" style={{overflowY:"scroll",height:"60%"}} >
                {teamNames.map((x,index) => {
                 
                    return (
                        <div className='col-md-2 col-sm-6' >
                            <div class="card  m-1 customCardHover" style={{ cursor: "pointer" }} onClick={()=>{
                                setSelectedIndex(index)
                            }}>
                                {index==selectedIndex?<div style={{position:"absolute",backgroundColor:"black",filter:"opacity(90%)"}}  class="h-100 w-100"></div>:""}
                                
                                <img src={x.ImagePath} style={{maxHeight:"120px",width:"100%"}} class="card-img-top" alt="..." />
                                <span style={{backgroundColor:"var(--primary)",maxHeight:"30px",overflow:"hidden",marginBottom:"0px !important",color:"var(--secoundary)",padding:"2%",textAlign:'center'}} >{x.ImageName}</span>
                            </div>
                           
                        </div>
                    )
                })}
            </div>
            <div className='row justify-content-center' style={{marginTop:"2%"}}>
                {selectedIndex>-1?<div className='btn btn-success col-md-2' onClick={()=>{
                    navigate(`/register/${teamNames[selectedIndex].ImageName}`)
                }}>Continue</div>:""}
            </div>
        </div>

    );
}

export default CHOOSETEAMANME;