import { useEffect } from 'react'
import {useForm} from 'react-hook-form'
import { useNavigate } from 'react-router-dom';
function EDITTEAMLOGIN() {
    const navigate = useNavigate()
    const { register, handleSubmit, watch, control, formState: { errors } } = useForm();
    const onSubmitCustom = (data)=>{
        if(data.CorpId==='Test' && data.Password==='Test'){
            navigate('/register/edit/a728685')
        }

        console.log(data)
    }
   useEffect(()=>{
     console.log(errors)
   },[])
    return (
        <div className="row h-100 justify-content-center align-items-center">
            <div className="col-md-4">
                <form className=" defaultBgBlack " style={{ borderRadius: "20px",padding:"20px" }}  onSubmit={handleSubmit(onSubmitCustom)}>
                    <div class="form-floating p-2">
                        <input type="text" class="form-control"  id="teamName" placeholder="Team Name" autoComplete={"off"} {...register("CorpId",{required:true})}  />
                        <label for="teamName" className='customLabel'>Team Name</label>
                        {errors?.CorpId && <span className='error'>This field is required</span>}
                    </div>
                    <div class="form-floating p-2">
                        <input type="password" class="form-control" id="teamCaptainCorpId" placeholder="Password" autoComplete={"off"}  {...register("Password",{required:true})} />
                        <label for="teamCaptainCorpId" className='customLabel'>Password</label>
                        {errors?.Password && <span className='error'>This field is required</span>}
                    </div>
                    <button className="btn btn-success " style={{display:"block",margin:"auto",marginTop:"2%"}}>Login</button>
                </form>
            </div>
        </div>
    );
}

export default EDITTEAMLOGIN;