
import './index.css'
import { Center, Text3D, Text } from '@react-three/drei'
import { Canvas, useFrame } from '@react-three/fiber';
import * as THREE from 'three'
import { useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import LOGO from '../../images/logo.png'
import TEAMLOGOINFO from '../../utils/teamlogo';
function HOMEPAGE() {
   useEffect(()=>{
      console.log(TEAMLOGOINFO());
  },[])
   const navigate = useNavigate();
   return (
      <div style={{ height: "100%", display: "flex" }}>

         <div className={"col-md-12 d-flex justify-content-center align-items-center"} style={{ position: "absolute", height: "80%" }}>
            <div className='d-flex customHeadingSection'>
               <img src={LOGO} style={{ height: "400px", width: "400px" }} />
               <h1 className='customHeading'>GET READY FOR ENCORE</h1>
               <div className='d-flex w-100 justify-content-center'>
                  <button className='customHeadingButton' onClick={() => {
                     navigate('/teamName')
                  }}>REGISTER YOUR TEAM</button>
                  <button className='customHeadingButton'    onClick={() => {
                     navigate('/EditLogin')
                  }}>EDIT YOUR TEAM   </button>
               </div>

            </div>

         </div>
      </div>
   );
}
const TEXT3D = () => {

   return (
      <Canvas orthographic camera={{ position: [0, 0, 100], zoom: 100 }} id="myCanvas" >
         <CUSTOMTEXT />
      </Canvas>
   )
}
const CUSTOMTEXT = () => {
   const myMesh = useRef()
   const myText = useRef();
   let defaultValue = .003
   let defaultRotation = .003
   useFrame(() => {
      debugger

      if (myMesh.current.position.y >= 1.5) {
         defaultValue = -.003
      }
      if (myMesh.current.position.y <= 1) {
         defaultValue = .003
      }

      console.log(myText.current.rotation.y)
   })
   return (
      <mesh position={[0, .1, 1]} ref={myMesh}>
         <spotLight position={[10, 10, 10]} angle={0.5} color={new THREE.Color('red')} />
         <spotLight position={[10, 10, 10]} angle={-0.5} color={new THREE.Color('red')} />
         <ambientLight intensity={.5} />
         <Center rotation={[.1, 0, 0]} ref={myText}>
            <Text3D font={"/Poppins_Bold.json"} curveSegments={32}  >
               {`LET BE READY `}
               <meshStandardMaterial color="#fafe01" shininess={150} emissive="#030202" roughness={0} metalness={.1} />
            </Text3D>
            <Text>
               {"Regiter your team"}
            </Text>
         </Center>
      </mesh>
   )
}
export default HOMEPAGE;